
import wx from 'weixin-js-sdk'
import {mapActions, mapGetters}from 'vuex'
import empty from '../components/empty'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: "toberealized",
data(){
return{
zpData:[],
flag:false,
onFetching: false,
listAll:[],
dataList:[],
sy:'',
ey:''
}
},
created(){

if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}

this.showLoading()
this.getShingCart({data:{cartType:'wishCart'},this:this,method:this.getShingCart})
this.addressList({this:this,method:this.addressList})
},
mounted(){
console.log(this.getadressLb)
},
components:{
Scroller, LoadMore ,empty
},
computed:{
...mapGetters(['getadressLb']),
checkAll(){
return this.dataList.length===this.listAll.length ? true :false
}
},
watch:{

},
methods:{
...mapActions(['getShingCart','addItemToShoppingCart','addWish','deleteCartItem','addressList','chooseCartItem']),
goToDe(id){
wx.miniProgram.navigateTo({
url:'/pages/goodDetail/index?id='+id
})
},
reLoad(){
this.showLoading()
this.getShingCart({data:{cartType:'wishCart'},this:this,method:this.getShingCart})
},
ontouchstart(e){
this.sy = e.changedTouches[0].pageY
},
ontouchend(e){
this.ey = e.changedTouches[0].pageY
if(this.sy - this.ey > 30){
this.onScrollBottom()
}
}  ,
ontouchstart1(e){
this.sy = e.changedTouches[0].pageX
},
ontouchend1(e){
this.ey = e.changedTouches[0].pageX
if(this.sy - this.ey > 30){//左滑
e.currentTarget.className = 'addLeft'
}
if(this.ey - this.sy > 10){//右滑
e.currentTarget.className = ''
}
},
del(id){
this.showLoading()
this.deleteCartItem({data:{product_ids:id,cartType:'wishCart'},this:this})
},
goToIndex(){
wx.miniProgram.switchTab({
url: '/pages/home/index'
})
},
toHlp(){
if(!this.listAll.length){
this.showTost('请选择商品')
return
}
let obj = this.dataList.filter( v => {
return  this.listAll.indexOf(v.productId) > -1
})
console.log(obj)
wx.miniProgram.navigateTo({
url: '/pages/helpFriends/index?orderType='+JSON.stringify(obj)
})
},
addCart(wishId){
wx.miniProgram.navigateTo({
url: '/pages/sureorder/index?orderType=wishCart&cartType=wishCart&wishId='+ wishId
})
return
let ls =  this.dataList.filter( v => {
return  this.listAll.indexOf(v.productId) > -1
})
let id = ls.map(v => v.productId)
let p = ls.map(v => v.displayPrice)
let vb = ls.map(v => 1)
if(id.length == 1){
let obj1 ={
add_product_id:id.join(),
quantity:vb.join(),
price:p.join(),
cartType:'wishCart'
}
this.addItemToShoppingCart({data:obj1,this:this,n:1,wishId:wishId})
}
else{
let obj ={
productIds:id.join(),
productNums:vb.join(),
productPrices:p.join(),
cartType:'wishCart'
}
this.addItemToShoppingCart({data:obj,this:this,wishId:wishId})
}
},
ziBuy(){
if(!this.listAll.length){
this.showTost('请选择商品')
return
}
let ls =  this.dataList.filter( v => {
return  this.listAll.indexOf(v.productId) > -1
})
let id = ls.map(v => v.productId)
this.addWish({this:this,data:{productIds:id.join(),contactMechId:this.getadressLb.defaultAddressId},method:this.addWish})
},
goTo(wishId){
wx.miniProgram.navigateTo({
url: '/pages/sureorder/index?orderType=wishCart&cartType=wishCart&wishId='+ wishId
})
},
addMe(e){
let sendData = this.dataList.map( v =>{
if(this.listAll.indexOf(v.productId) > -1){
return {product_id:v.productId,isChoose:'Y'}
}
else{
return {product_id:v.productId,isChoose:'N'}
}
})
sendData.push({cartType:'wishCart'})
this.showLoading()
this.chooseCartItem({data:JSON.stringify(sendData),this:this,method:this.chooseCartItem})
},
getData(data){
this.flag = true
this.listAll = []
this.dataList = data.filter( v => {//商品
return !v.isGift || v.isGift == 'N'
})
this.zpData = data.filter( v => {//赠品
return  v.isGift == 'Y'
})
this.dataList.forEach(v => {
if(v.isChoose == 'Y'){
this.listAll.push(v.productId)
}
})
},
isSal(){
if(this.dataList.length===this.listAll.length){
this.listAll =[]
}
else{
this.listAll = []
this.dataList.forEach(v => {
this.listAll.push(v.productId)
})
}
this.addMe()
},
handleEvents (type) {
console.log('event: ', type)
},
onScrollBottom () {
if (this.onFetching) {

} else {
this.onFetching = true
setTimeout(() => {

this.$nextTick(() => {

})
this.onFetching = false
}, 2000)
}
},
}
}



